.add-btn {
	margin-bottom: 2rem;
}

.fade-enter {
	opacity: 0.02;
}

.fade-enter-active {
	opacity: 1;
	-webkit-transition: opacity 900ms ease-in;
	transition: opacity 900ms ease-in;
}

.fade-exit {
	opacity: 1;
}

.fade-exit-active {
	opacity: 0;
	-webkit-transition: opacity 1000ms ease-in;
	transition: opacity 1000ms ease-in;
}

.input-margin {
	margin-bottom: 1rem;
}

.card-margin {
	margin-bottom: 1.5rem;
}
